.resume {
  display: flex;
  flex-direction: column;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  color: darkgray;
  font-size: 25px;  
  margin-left: 70px;
  margin-right: 70px;    
}

.resume-p a { 
  border-radius: 25px;  
  text-decoration: none; 
  font-weight: 600;
  padding: 0px 3px;
}

.resume-p a:hover{
  color: white;
  background-color: rgb(106, 78, 125);
  padding: 5px 10px;
 
}

.resume-section {
  display: flex;
  flex-direction: column;
  justify-content: left;  
}


.resume-sec-head {
  font-size: 25px;
  margin-bottom: 0px; 
}

.resume-list {
  display: flex;
  flex-direction: column; 
  font-size: 25px;    
}