  .footer{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;   
  }
  .foot {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;   
 
  }

  .foot-item a{      
     text-decoration: none;         
  }
  
  .footer ul {
    display: flex;      
    justify-content: center;
    align-items: center;
    width: 60%;   
    list-style-type: none;   
    text-align: center;
    
  }

  .foot li {      
    min-width: 25%;  
   
  }

  .github-grey-icon { 
    color: darkgray;
    margin-top: 0;
    font-size: 55px;
 }


  @media only screen and (max-width: 780px) {
    .footer ul {
      justify-content: center;
      flex-wrap:wrap;      
    }

    .foot-item a {      
      padding: 0px 10px;
      border-radius: 25px;
      }
  }    

@media only screen and (max-width: 640px) {  
  .foot-item a {
  padding: 0px 10px;
  border-radius: 25px;
  }
}

@media only screen and (max-width: 515px) {  
  .foot-item a {
    font-size: 25px;      
  }
}